import React from 'react';
import _ from 'lodash';
// import PropTypes from 'prop-types';

const getClass = status => {
  switch (status) {
    case 'Checked':
      return 'NotChecked';
    case 'Checking':
      return 'CheckNow';
    case 'ToDo':
      return 'ToDoNow';
    case 'In Progress':
      return 'Progress';
    default:
      return '';
  }
};

const getStatusCells = (tasks, students) => {
  return _.map(tasks, (item, indx) => {
    const taskKey = item.Name.match(/[a-z0-9]/gi)
      .join('')
      .toLowerCase();
    const row = _.map(students, (student, index) => {
      if (student.Tasks && student.Tasks[taskKey]) {
        const title = `Comment: ${
          student.Tasks[taskKey].Comment
        }\nClick to go to PR`;
        return (
          <td
            id={student.Tasks[taskKey].PR}
            className='Checked'
            title={title}
            key={`status-${index}`}
          >
            +
          </td>
        );
      } else if (!student.Tasks) {
        const className = getClass(item.Status);
        return (
          <td className={className} title='Not Exist' key={`status-${index}`}>
            o
          </td>
        );
      } else {
        const className = getClass(item.Status);
        return (
          <td
            className={className}
            title='Mark not found'
            key={`status-${index}`}
          >
            o
          </td>
        );
      }
    });
    return <tr key={`stusrow-${indx}`}>{row}</tr>;
  });
};

const getStudentsName = arr => {
  return arr.map((item, index) => {
    return (
      <th className='Student' key={index}>
        <a
          href={`https://github.com/${item.Nick}`}
          target='_blank'
          rel='noopener noreferrer'
        >
          {item.Nick}
        </a>
      </th>
    );
  });
};

class RightPartOfTable extends React.Component {
  goToPR(event) {
    const url = event.target.id;
    if (url) {
      const win = window.open(url, '_blank');
      win.focus();
    }
  }

  render() {
    const tableBody = getStatusCells(
      this.props.taskArr,
      this.props.studentsArr
    );
    const studentsName = getStudentsName(this.props.studentsArr);
    return (
      <div>
        <h2>Students</h2>
        <table onClick={this.goToPR.bind(this)}>
          <thead>
            <tr>{studentsName}</tr>
          </thead>
          <tbody>{tableBody}</tbody>
        </table>
      </div>
    );
  }
}

