import React from 'react';
import PropTypes from 'prop-types';

class LeftPartOfTable extends React.Component {
  getTableBody = arr => {
    return arr.map((item, index) => {
      const status = item.Status;
      const taskClass = status.match(/[a-z]/gi).join('');
      return (
        <tr key={index}>
          <td className={taskClass} title={item.Status}>
            <a href={`${item.Link}`} target='_blank' rel='noopener noreferrer'>
              {item.Name}
            </a>
          </td>
        </tr>
      );
    });
  };

  render() {
    const TableBody = this.getTableBody(this.props.tasks);
    return (
      <div>
        <h2>Tasks</h2>
        <table>
          <tbody>
            <tr>
              <td className='notSeen'>.</td>
            </tr>
            {TableBody}
          </tbody>
        </table>
      </div>
    );
  }
}

LeftPartOfTable.propTypes = {
  tasks: PropTypes.array,
};

export default LeftPartOfTable;
