import React from 'react';
import './index.scss';
import RSSBandLogo from './RSSBands.svg';
import reactLogo from './logo.svg';
import PropTypes from 'prop-types';

class Header extends React.Component {
  render() {
    return (
      <header>
        <img src={RSSBandLogo} className='Header-logo' alt='logo' />
        <h1>{this.props.headline}</h1>
        <img src={reactLogo} className='Header-logo Header-anim' alt='logo' />
      </header>
    );
  }
}

Header.propTypes = {
  headline: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
    .isRequired,
};
Header.defaultProps = {
  headline: 'MENTOR DASHBOARD',
};
export default Header;
