import React from 'react';
import _ from 'lodash';
import './index.scss';
import LeftPartOfTable from './leftPart';
import RightPartOfTable from './rightPart';

class Table extends React.Component {
  render() {
    if (!this.props.tableData.students) {
      return <div />;
    } else {
      const taskArr = this.props.tableData.tasks;
      const studentsArr = _.sortBy(_.values(this.props.tableData.students), [
        'Tasks',
        'Nick',
      ]);
      return (
        <div className='Table'>
          <LeftPartOfTable tasks={taskArr} />
          <RightPartOfTable taskArr={taskArr} studentsArr={studentsArr} />
        </div>
      );
    }
  }
}

export default Table;
